import React, { Component } from 'react'
import PropTypes from 'prop-types';
import axios from 'axios';
import {Link} from 'react-router-dom'

const styles = {
    card: {
        width: '18rem'
    },
    image: {
        height: '200px'
    }
}

class ValstybesAtvaizdavimas extends Component {
    render() {
        var deleteItem = () =>{
           
            axios.delete("http://localhost:8181/api/valstybes/" + 
            this.props.pavadinimas)
            .then( res => {
                console.log("form is submited, bellow is return:");
                console.log(res);
             
                this.props.updateState()
  

            }).catch(
                error =>{
                    console.log("bellow error message");
                    console.log("error");
                }
            )
        }

    
        
        return (
            <div className="card" style={styles.card}>
                <img className="card-img-top"
                    src={this.props.paveiksliukas}
                    alt={this.props.pavadinimas}
                    style={styles.image} />
                <div className="card-body">
                    <h5 className="card-title">{this.props.pavadinimas}</h5>
                    <p className="card-text">{this.props.aprasymas} </p>
                    <p>Pavadinimas {this.props.pavadinimas} </p>
                    <p>Prezidentas {this.props.president}</p>
                    <p>Kategorija {this.props.kategorija}</p>
           
                    <button type="button" className="btn btn-primary">Primary</button>
    
          
                    <a onClick={deleteItem} className="btn btn-warning">Delete</a>
   
                </div>
            </div>
                );
    }
}

