import React, { Component } from 'react'
import PropTypes from 'prop-types';
import axios from 'axios';
import nuotrauka1 from '../pictures/pic.jpg';
import nuotrauka2 from '../pictures/pic2.jpg';
import nutrauka3 from '../pictures/pic3.jpg';
import ValstybesAtvaizdavimas from './ValstybesAtvaizdavima';
import {Link} from 'react-router-dom'
import { stringify } from 'querystring';

class SventeDetailed extends Component {
    constructor(props){
        super(props)
        this.state = {
        objektai: []
        }
    }

    componentDidMount = () => {
        // this.setState({pavadinimas: this.props.match.params})
        // console.log(this.props.match.params.value);
        const adress =this.props.match.params.pavadinimas;
        console.log(this.props.match.params.pavadinimas);

        axios.get("http://localhost:8181/api/sventes/"
         + adress  +"/get-valstybes" )
        .then((atsakymas) => {
            this.setState({objektai: atsakymas.data})
            console.log(atsakymas.data);
          })
        .catch((klaida) => {
            console.log(klaida)
          })
        
    }

    render() {
        console.log("Objektas " + this.state.pavadinimas);
        var objektaiToDisplay = this.state.objektai.map(objektas => {
            var paveiksliukoObjektas = nuotrauka1;
            if (objektas.flag === "string") {
                paveiksliukoObjektas = nuotrauka2;
            }
            if (objektas.flag === "lietuva") {
                paveiksliukoObjektas = nutrauka3;
            }
            return (<div className="col-sm-4" key={objektas.pavadinimas}>

                <ValstybesAtvaizdavimas
                         paveiksliukas={paveiksliukoObjektas}
                         pavadinimas={objektas.name}
                         president={objektas.president}
                         updateState={this.componentDidMount}
                         />
            </div>);
        })

        var deleteItem = () =>{
            
            let body = this.state;
            axios.delete("http://localhost:8181/api/sventes/" + 
            this.props.pavadinimas)
            .then( res => {
            console.log(res);
            }).catch(
                error =>{
                    console.log("bellow error message");
                    console.log("error");
                }
            )
        }
        
        return (
            <div>
                produktai cia
                <button className="btn btn-primary m-4" onClick={this.objektaiToDisplay}>press</button>
                <div className="container-fluid">
                    <div className="row">
                    {objektaiToDisplay}
                    </div>
                </div>
            </div>
                );
    }
}

