import React from 'react';
import { throws } from 'assert';
import axios from 'axios';


class Form extends React.Component{
    state ={
        name: "test",
        description: "test",
        picture: "test",
        type: "test",
        flagUpTrue: "True"
        // number: parseInt(Math.round(Math.random() *100000))
    }
    handleUserInput(e){
        const name= e.target.name;
        const value=e.target.value;
        this.setState({[name]:value})
    }
    formIsSubmited(e){
        e.preventDefault();
        let body = this.state;
        axios.post("http://localhost:8181/api/sventes/add", body)
        .then( res => {
            console.log("form is submited, bellow is return:");
            console.log(res);
            //updates display of elements in main body

            // resets form after input
            this.setState({        
                name: "",
                description: "",
                picture: "",
                type: "",
                flagUpTrue: "True"
        })
        }).catch(
            error =>{
                console.log("bellow error message");
                console.log("error");
            }
        )
    }

    render(){
    return( 
    <div className="Container m-4 ">
        <form>
            <div className="form-group justify-content-center">
                <label htmlFor="formGroupExampleInput">
                name</label>
                <input 
                onChange={e => this.handleUserInput(e)}
                value={this.state.name}
                // name from state is name= {this.name}
                name="name" 
                type="text" 
                className="form-control" 
                id="formGroupExampleInput" 
                placeholder="Example input"/>
            </div>

            <div className="form-group">
                <label htmlFor="formGroupExampleInput">
                description</label>
                <input 
                onChange={e => this.handleUserInput(e)}
                value={this.state.description}
                // name from state is name= {this.description}
                name="description" 
                type="text" 
                className="form-control" 
                id="formGroupExampleInput" 
                placeholder="Example input"/>
            </div>

            <div className="form-group">
                <label htmlFor="formGroupExampleInput">
                type</label>
                <input 
                onChange={e => this.handleUserInput(e)}
                value={this.state.type}
                // name from state is name= {this.imageDesc}
                name="type" 
                type="text" 
                className="form-control" 
                id="formGroupExampleInput" 
                placeholder="Example input"/>
            </div>

            <div className="form-group">
                <label htmlFor="formGroupExampleInput">
                picture</label>
                <input 
                onChange={e => this.handleUserInput(e)}
                value={this.state.picture}
                // name from state is name= {this.imageDesc}
                name="picture" 
                type="text" 
                className="form-control" 
                id="formGroupExampleInput" 
                placeholder="Example input"/>
            </div>
    <button className="btn btn-success"
      type="button" onClick={this.formIsSubmited.bind(this)}>SubmitForm</button>
    
        </form>
        {/* <Instituvija /> */}
    </div>);

    }

}
export default Form;