import React, { Component } from 'react'
import PropTypes from 'prop-types';
import axios from 'axios';
import {Link} from 'react-router-dom'

const styles = {
    card: {
        width: '18rem'
    },
    image: {
        height: '200px'
    }
}

class SvenciuAtvaizdavimas extends Component {
    render() {
        var deleteItem = () =>{
            
            let body = this.state;
            axios.delete("http://localhost:8181/api/sventes" + 
            this.props.pavadinimas)
            .then( res => {
                console.log("form is submited, bellow is return:");
                console.log(res);
            }).catch(
                error =>{
                    console.log("bellow error message");
                    console.log("error");
                }
            )
        }

        let linkas = this.props.pavadinimas + "/get-products" ;
        
        return (
            <div className="card" style={styles.card}>
                <img className="card-img-top"
                    src={this.props.paveiksliukas}
                    alt={this.props.pavadinimas}
                    style={styles.image} />
                <div className="card-body">
                    <p>Sventes Pavadinimas {this.props.pavadinimas} </p>
                    <p>Description: {this.props.description} </p>
                    <p>Type: {this.props.type} </p>
                    <Link className="btn btn-primary" to={linkas}>press for detail 
                    </Link>

                    <button onClick={deleteItem} className="btn btn-warning">Delete</button>

                   

                </div>
            </div>
                );
    }
}

export default SvenciuAtvaizdavimas;