import React from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Sventes from './Sventes';
import SventeDetailed from './SventeDetailed';
import SventesForma from './SventesForma';
import ValstybesForm from './SventesForma';
import ValstybiuAtvaizdavimas from './Valstybes';

function BasicNavigation() {
  return (
    <Router>
      <div>
        <ul>
          <li>
            <Link to="/">Home</Link>
          </li>
          <li>
            <Link to="/formsvente">Prideti Institucija</Link>
          </li>
          <li>
            <Link to="/formvalstybe">Prideti Valstybe</Link>
          </li>
          <li>
            <Link to="/displayvalstybes">Visos Valstybes</Link>
          </li>
        </ul>

        <hr />

        <Route exact path="/" component={DisplayHome} />
        <Route path="/formsvente" component={FormRun2} />
        <Route path="/formvalstybe" component={FormRun} />
        <Route path="/displayvalstybes" component={displayvalstybes} />
        <Route path="/:pavadinimas/get-valstybes" component={SventeDetailed} /> 
      </div>
    </Router>
  );
}

function displayvalstybes() {
  return (
    <div>
      <ValstybiuAtvaizdavimas/>
    </div>
  );
}

function FormRun() {
  return (
    <div>
      <ValstybesForm/>
    </div>
  );
}


function FormRun2(){
  return (
    <div>
      <SventesForma/>
    </div>
  );
}
function DisplayHome(){
    return (
        <div>
          <div>
            <Sventes/>
          </div>
        </div>
    );
}




export default BasicNavigation;
