import React, { Component } from 'react';
import axios from 'axios'
import nuotrauka1 from '../pictures/pic.jpg';
import nuotrauka2 from '../pictures/pic2.jpg';
import nutrauka3 from '../pictures/pic3.jpg';
import SvenciuAtvaizdavimas from './SvenciuAtvaizdavimas';

class Sventes extends Component {
    constructor(props) {
        super(props);
        this.state = {
            objektai: [] ,
            sventeName: "string"
        }
    }
    render() {
        if (this.state.objektai.length === 0) {
            return <div>Dar neuzkorviau duomenu.. Prasau palaukti..</div>
        }
        var objektai = this.state.objektai.map(objektas => {
            var paveiksliukoObjektas = nuotrauka1;
            if (objektas.picture === "string") {
                paveiksliukoObjektas = nuotrauka2;
            }
            if (objektas.picture === "jonines") {
                paveiksliukoObjektas = nutrauka3;
            }
            return (<div className="col-sm-4" key={objektas.name}>
                    <SvenciuAtvaizdavimas
                    paveiksliukas={paveiksliukoObjektas}
                    pavadinimas={objektas.name}
                    type={objektas.type}
                    description={objektas.description}
                    key={objektas.name}
                   />
            </div>);
        });

    
        return (
                <div>
                <div className="container-fluid">
                    <div className="row">
                        {objektai}
                    </div>
                </div>
                </div>);
    }
    componentDidMount = () => {
        console.log(this.state.sventeName);
        axios.get("http://localhost:8181/api/sventes/all")
          .then((atsakymas) => {
              this.setState({objektai: atsakymas.data})
              console.log(this.state);
            })
          .catch((klaida) => {
              console.log(klaida)
            })
    }

}


export default Sventes;